<template>
  <div id = "cart">
    <CartNavBar :length = "length"/>
    <Scroll class = "scroll" ref = "scroll">
      <CartGood v-for = "(item,index) in list" :good = "item" :key = "index"/>
    </Scroll>
    <CartBuy @buy = "buy"/>
  </div>
</template>

<script>
  // 
  import CartNavBar from "./childrencomps/CartNavBar";
  import CartGood from "./childrencomps/CartGood";
  import CartBuy from "./childrencomps/CartBuy";
  // 
  import Scroll from "components/common/scroll/Scroll";
  import { mapGetters } from "vuex"
  // 
  export default {
    name: "Cart",
    components: {
      CartNavBar,
      Scroll,
      CartGood,
      CartBuy
    },
    activated() {
      this.$refs.scroll.refresh()
    },
    computed: {
      ...mapGetters({
        length: "cartLength",
        list: "cartList"
      })
    },
    methods: {
      buy() {
        if (this.$store.state.cartList.find(item => item.isChecked)) {
          this.$toast.show("", 4000)
        } else {
          this.$toast.show("", 4000)
        }
      }
    }
  }
</script>

<style scoped>
  #cart {
    height: 100vh;
    position: relative;
  }

  .scroll {
    position: fixed;
    top: 44px;
    height: calc(100% - 49px - 45px - 44px);
  }
</style>




